import { IonicModule } from 'ionic-angular';
import { NgModule } from '@angular/core';
// import { EmailService } from '../../services/email.service';
// import { CallService } from '../../services/call.service';
import { HomePage } from './home.page';
import { CuentaComponent } from "../cuenta/cuenta.component";
import { NovedadesComponent } from "../video/novedades/novedades.component"
import { ProgramasComponent } from "../video/programas/programas.component"
import { MarcadoresComponent } from "../video/marcadores/marcadores.component"
import { VideoItem } from '../video/videoItem/videoItem.component'
import { CanalItem } from '../video/canalItem/canalItem.component'
import { PipesModule } from '../../pipes/pipes.module';
import { SocialSharing } from '@ionic-native/social-sharing';
// import { InAppBrowserService } from '../../services/in-app-browser.service';
// import { MapsService } from '../../services/maps.service';

@NgModule({
	imports: [IonicModule,PipesModule],
	declarations: [
		HomePage, 
		CuentaComponent,
		NovedadesComponent,
		ProgramasComponent,
		VideoItem,
		CanalItem,
		MarcadoresComponent
		],
	entryComponents: [
		HomePage, 
		CuentaComponent,
		NovedadesComponent,
		ProgramasComponent,
		VideoItem,
		CanalItem,
		MarcadoresComponent
		],
	providers: [
		SocialSharing
		
	]
})
export class HomeModule {

}


