import { Component, OnInit } from '@angular/core';
import { Platform, Nav} from 'ionic-angular';
import { InAppBrowser } from "ionic-native";

import { EntornoConfig } from "../../constants/entorno.config";

import { SharedService } from '../../services/shared.service';
import { ProgramaService } from '../../services/programa.service';

import { LoginComponent } from '../login/login.component';
import { AppVersion } from '@ionic-native/app-version';

import { GoogleAnalytics } from '@ionic-native/google-analytics';


@Component({
    selector: 'cuenta',
    templateUrl: './cuenta.component.html',
    providers: [SharedService, EntornoConfig, ProgramaService, AppVersion]
})
export class CuentaComponent implements OnInit {
    usuario: any;
    programas: any[] = [];
    version:string;
    
    constructor(
        private platform: Platform,
        private sharedService: SharedService,
        private nav: Nav,
		private entornoConfig: EntornoConfig,
        private programaService: ProgramaService,
        private appVersion: AppVersion,
        private ga: GoogleAnalytics
        ){}

    ngOnInit(){
        this.ga.trackView('View: Cuenta');
        //this.ga.trackException('Test Exception',true);       
        //(window as any).FirebaseCrashReport.log("test Boom!");
        this.usuario = this.sharedService.usuario;
        this.programaService.Favoritos(1, 10)
        .subscribe(respuesta => {
				  if (respuesta.Result != "")
				this.programas = respuesta.Result;
            else
                this.programas =[];
			},
			error => { console.log(error) });
        this.appVersion.getVersionNumber().then(v=>{
            this.version = v
        });
    }

	doRefresh(refresher) {
		this.ga.trackEvent('Bookmark', 'Refresh');
        this.programaService.Favoritos(1, 10)
        .subscribe(respuesta => {
            if (respuesta.Result != "")
				this.programas = respuesta.Result;
            else
                this.programas =[];
                refresher.complete(); 
			},
			error => { console.log(error); refresher.complete();  });
        this.appVersion.getVersionNumber().then(v=>{
            this.version = v
           
        });
	}

    launch(url) {
        this.platform.ready().then(() => {
            let browser = new InAppBrowser(url, "_blank");
        });
    }

    salir(){
        this.ga.trackEvent('Cuenta', 'LogOff');
        this.sharedService.insertUsuario('','');
        this.nav.setRoot(LoginComponent);
    }

	ObtenerImagenProgramaPrincipal(imagen): any {
		return this.sharedService.Core + "/archivo/ObtenerImagenPorId/" + imagen + '|height-100|width-100|mode-crop|quality-100|';
	}
}


